
.main {
  font-family: "PingFangSC-Regular";
  text-align: left;
  color: #333;
  font-size: .32rem;
  -webkit-box-flex: 1;
  overflow-y: auto;
}
.main .nav-list li {
    border-bottom: 1px solid #ddd;
    height: 1.2rem;
    line-height: 1.2rem;
    margin-left: .3rem;
    position: relative;
}
.main .nav-list li a {
      font-size: .32rem;
      display: block;
}
.main .nav-list li a * {
        vertical-align: middle;
}
.main .nav-list li .icon {
      width: .4rem;
      height: .4rem;
      display: inline-block;
      margin: 0 .44rem 0 .15rem;
      height: .36rem;
      margin-right: .3rem;
}
.main .nav-list li .icon-info {
      position: absolute;
      right: 0rem;
      top: 50%;
      margin: -0.15rem 0 0 0;
}
.main .nav-list li .iconn-5:before {
      color: #666;
}
.main .nav-list li b {
      position: absolute;
      right: .28rem;
      top: 50%;
      margin-top: -0.12rem;
      height: .24rem;
      width: .13rem;
}
.main .nav-list .clips {
    height: .1rem;
    background-color: #f3f5f7;
}
.main .nav-list .content-box li strong {
    font-size: .28rem;
    color: #666;
    float: right;
    font-weight: normal;
    margin-right: .5rem;
}
.main .nav-list .content-box .search-li * {
    vertical-align: middle;
}
.main .nav-list .content-box .search-li em {
    height: .32rem;
    width: .32rem;
    display: inline-block;
    margin: 0 .1rem 0 .1rem;
}
.main .nav-list .content-box .search-li input[type="text"] {
    color: #333;
    font-size: .32rem;
    width: 90%;
    height: .8rem;
}
.main .nav-list .content-box .search-li input[type="text"]::-webkit-input-placeholder {
      color: #999;
}
.main .nav-list .content-box .btn-box {
    margin-top: .5rem;
    padding: 0 .3rem;
}
.main .nav-list .content-box .btn-box button {
      display: block;
      height: .88rem;
      width: 100%;
      border-radius: .12rem;
      background-color: #bfbfbf;
      color: #fff;
      font-size: .32rem;
}
.main .nav-list .content-box .btn-box .on {
      background-color: #d63333;
}
.main .nav-list .load-more {
    height: .9rem;
    line-height: .9rem;
    color: #999;
    font-size: .3rem;
    text-align: center;
}
.main .nav-list .load-more * {
      vertical-align: middle;
}
.main .nav-list .load-more .icon {
      margin-left: .08rem;
}
.main .nav-list .navListItemTitle {
    margin-right: .42rem;
    color: #b9b9b9;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
}
.main .nav-list .navListItemTitle strong {
      color: #333;
      font-weight: normal;
      margin-right: .16rem;
}
.main .search-head {
    height: .8rem;
    line-height: .8rem;
}
.main .search-head span {
      display: inline-block;
      width: 49%;
      text-align: center;
      height: .3rem;
      line-height: .3rem;
}
.main .search-head .on {
      color: #d63333;
}
.main .search-head .filter {
      border-right: 1px solid #ddd;
}



