
.detailMain {
  width: 100%;
}
.detailMain .detailtitle {
    width: 100%;
    padding: .52rem .3rem 0;
    text-align: center;
}
.detailMain .detailtitle .detailflag {
      float: left;
      width: .08rem;
      height: .24rem;
      margin: .14rem 0;
      background-color: #d63333;
}
.detailMain .detailtitle .detailname {
      float: left;
      width: 90%;
      text-indent: .06rem;
      line-height: .5rem;
      font-size: .36rem;
      text-align: center;
}
.detailMain .detailcontainer {
    width: 7rem;
    padding: .22rem .36rem 0;
}
.detailMain .detailcontainer ol li {
      /*ie6-ie9*/
}
.detailMain .detailcontainer ol li .detailItems {
        line-height: .7rem;
        font-size: .28rem;
        color: #666;
}
.detailMain .detailcontainer ol li .fl {
        float: left;
}
.detailMain .detailcontainer ol li .detailexplainItems {
        line-height: .7rem;
        font-size: .28rem;
        color: #151515;
}
.detailMain .detailcontainer ol li .detailmemberItems {
        float: left;
        line-height: .7rem;
        font-size: .28rem;
        color: #4990e2;
}
.detailMain .detailcontainer ol li .detailprogressCount {
        float: left;
        line-height: .7rem;
        font-size: .28rem;
        color: #f6a623;
        margin-left: .13rem;
}
.detailMain .detailcontainer ol li .detailprogressItems {
        width: 50%;
        height: .1rem;
        margin: .28rem 0;
        background-color: #fff;
        display: inline-block;
        border: 1px solid #f6a623;
        color: #f6a623;
        /*IE10*/
}
.detailMain .detailcontainer ol li progress ie {
        display: block;
        height: 100%;
        background: #f6a623;
}
.detailMain .detailcontainer ol li progress::-moz-progress-bar {
        background: #f6a623;
}
.detailMain .detailcontainer ol li progress::-webkit-progress-bar {
        background: #fff;
}
.detailMain .detailcontainer ol li progress::-webkit-progress-value {
        background: #f6a623;
}
.detailMain .detailcontainer .detailwrapper {
      width: 100%;
      overflow-x: scroll;
}
.detailMain .detailcontainer .detailwrapper .detaildrawing {
        width: 180%;
        height: 5.03rem;
}



