
.header {
  position: relative;
  width: 100%;
  height: 1.28rem;
  line-height: 1.5rem;
  font-size: 0.4rem;
  font-family: 'PingFangSC-Regular';
  text-align: center;
  background-color: #e7001b;
}
.header .header-backicon {
    position: absolute;
    top: .3rem;
    left: .1rem;
    width: .44rem;
    height: .44rem;
    padding: .2rem;
    line-height: .44rem;
    font-size: .44rem;
    vertical-align: middle;
}
.header span.header-title {
    color: #fff;
}



